.wrapper_UserHomePage{
    padding: 20px;
    position: relative;
    button{
        height: 25px;
        white-space: nowrap;
        padding: 1px 10px;
        font-weight: 600;
        font-size: 11px;
        line-height: 16px;
        background: none;
        color: #129575;
        &:hover{
            background-color: #129575;
            color: white;
        }
    }
    header{
        margin-top: 40px;
        display: flex;
        justify-content: space-between;
        align-items: center;
        div{
            h1{
                font-weight: 600;
                font-size: 20px;
                line-height: 30px;
                margin-bottom: 5px;
            }
            p{
                font-family: 'Poppins-Ragular';
                font-style: normal;
                font-weight: 400;
                font-size: 11px;
                line-height: 16px;  
                color: #A9A9A9;
            }
        }
    }
    .container_filter{
        display: flex;
        overflow: auto;
        gap: 10px;
        padding-bottom: 5px;
        &::-webkit-scrollbar {
            width: 12px;
            height: 12px;
            }
        &::-webkit-scrollbar-track {
            border: 1px solid #129575;
            border-radius: 10px;
            }
        &::-webkit-scrollbar-thumb {
            background: #129575;  
            border-radius: 10px;
            }
            
        &::-webkit-scrollbar-thumb:hover {
            background: #0a7a5e;  
            }
        .button_categories{
            
            }
           
    }
    .search_section{
      display: flex;
      justify-content: space-between;
      margin: 30px 0 15px 0;
        input{
            width: 80%;
            height: 40px;
            padding-left: 10px;
            border: 1.3px solid #D9D9D9;
            border-radius: 10px;
            &::placeholder{
                color: #0a7a5e44;
            }
        }
        button{
            height: 40px;
            padding: 0 10px;
            background-color:  #129575;
            &:hover{
                background-color: #0a7a5e;
            }
        }
    }
}


.card_container_homepage{
    width: 150px;
    font-size: 10px;
}

.popup_filter{
    display: none;
    position: absolute;
    top: 0;  
    left: 0;
    background-color: #a9a9a9be;
    height: 100%;
    width: 100%;
    button{
        border: 1px solid #129575;
    }
    .popup_container_filter{
        width: 60%;
        margin: 30px auto;
       
        background-color: white;
        border-radius: 30px;
        padding: 20px;
        h2{
            font-weight: 600;
            font-size: 14px;
            line-height: 21px;
            text-align: center;
        }
        & > div{
            margin: 20px 0;
        }
        & > div h2{
            text-align: start;
            margin-bottom: 5px;
        } 
        .button_time,
        .button_category,
        .button_rate{
            display: flex;
            flex-wrap: wrap;
            gap: 5px;
            button{
               img{
                margin-left: 7px;
               }
            }
        }
        .filterOk{
            text-align: center;
            button{
                height: 37px;
                min-width: 100px;
            }
        }
    }
    
}